import React, { useState } from "react";
import {
  registerUser,
  validateRegisterForm,
  RegisterRequest,
} from "../../api/user/registerApi";
import "./RegisterPage.css";

const RegisterPage: React.FC = () => {
  const [form, setForm] = useState({
    username: "",
    password: "",
    confirmPassword: "",
    firstName: "",
    lastName: "",
    email: "",
    phoneNumber: "",
    dateOfBirth: "",
    gender: "",
  });
  const [message, setMessage] = useState<string>(
    "Cho mng bn n vi knh ng k!"
  );
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [errors, setErrors] = useState<string[]>([]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setForm({ ...form, [name]: value });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsLoading(true);
    setMessage("ang x l ng k...");
    setErrors([]);

    // Kim tra mt khu khp nhau
    if (form.password !== form.confirmPassword) {
      setErrors(["Mt khu v nhp li mt khu khng khp"]);
      setMessage("Vui lng kim tra li thng tin!");
      setIsLoading(false);
      return;
    }

    // Validate form (loi b confirmPassword trc khi validate)
    const { confirmPassword, ...formData } = form;

    // Debug: Kim tra d liu trc khi gi
    console.log("Original form:", form);
    console.log("Data to send:", formData);

    const validationErrors = validateRegisterForm(formData as RegisterRequest);
    if (validationErrors.length > 0) {
      setErrors(validationErrors);
      setMessage("Vui lng kim tra li thng tin!");
      setIsLoading(false);
      return;
    }

    try {
      console.log("Form data to send:", formData); // Debug log
      const result = await registerUser(formData as RegisterRequest);
      if (result.success) {
        setMessage(
          " ng k thnh cng! Hy m mail v xc thc  hon tt ng k."
        );
        setErrors([]); // Clear any previous errors
      } else {
        setMessage(` ${result.message}`);
        setErrors([result.message]);
      }
    } catch (error) {
      console.error("Registration error:", error);
      const errorMsg =
        error instanceof Error
          ? error.message
          : "C li xy ra, vui lng th li!";
      setMessage(` ${errorMsg}`);
      setErrors([errorMsg]);
    }

    setIsLoading(false);
  };

  return (
    <div className="chat-register-container">
      <div className="chat-box">
        <div className="register-logo">
          <img 
            src="http://res.cloudinary.com/dytdhvf3s/image/upload/v1760508458/User_5e3ca275-4ec3-45c3-8f53-f173871d002e.jpg" 
            alt="ChatWeb Logo" 
          />
        </div>
        <div className="chat-message">{message}</div>
        {errors.length > 0 && (
          <div className="error-messages">
            {errors.map((error, index) => (
              <div key={index} className="error-message">
                 {error}
              </div>
            ))}
          </div>
        )}
        <form className="chat-form" onSubmit={handleSubmit}>
          <input
            type="text"
            name="username"
            placeholder="Tn ng nhp"
            value={form.username}
            onChange={handleChange}
            required
          />
          <input
            type="text"
            name="firstName"
            placeholder="H"
            value={form.firstName}
            onChange={handleChange}
            required
          />
          <input
            type="text"
            name="lastName"
            placeholder="Tn"
            value={form.lastName}
            onChange={handleChange}
            required
          />
          <input
            type="email"
            name="email"
            placeholder="Email"
            value={form.email}
            onChange={handleChange}
            required
          />
          <input
            type="password"
            name="password"
            placeholder="Mt khu"
            value={form.password}
            onChange={handleChange}
            required
          />
          <input
            type="password"
            name="confirmPassword"
            placeholder="Nhp li mt khu"
            value={form.confirmPassword}
            onChange={handleChange}
            required
          />
          <input
            type="tel"
            name="phoneNumber"
            placeholder="S in thoi"
            value={form.phoneNumber}
            onChange={handleChange}
            required
          />
          <div className="gender-options">
            <label>
              <input
                type="radio"
                name="gender"
                value="true"
                checked={form.gender === "true"}
                onChange={handleChange}
                required
              />
              Nam
            </label>
            <label>
              <input
                type="radio"
                name="gender"
                value="false"
                checked={form.gender === "false"}
                onChange={handleChange}
              />
              N
            </label>
          </div>

          <input
            type="date"
            name="dateOfBirth"
            placeholder="Ngy sinh"
            value={form.dateOfBirth}
            onChange={handleChange}
            required
          />
          <button type="submit" className="send-btn" disabled={isLoading}>
            {isLoading ? " ang ng k..." : " ng k"}
          </button>
        </form>
      </div>
    </div>
  );
};

export default RegisterPage;
