import React, { useCallback, useEffect, useMemo, useState, useRef } from "react";
import axios from "axios"; // still used for initial fetch only
import { useParams, useNavigate, NavLink } from "react-router-dom";
import {
  Mail,
  Phone,
  Calendar,
  User as UserIcon,
  ShieldCheck,
  Clock,
  Hash,
  Edit,
  Save,
  X,
  Users,
  MessageCircle,
  MoreHorizontal,
  UserPlus,
  Image as ImageIcon,
  Plus,
} from "lucide-react";
import { getUserInfo } from '../../api/user/loginApi';
import { changeAvatar } from '../../api/user/avatarApi';
import { updateUserProfile, UpdateProfileRequest as UpdateProfilePayload } from '../../api/user/profileApi';
import { getPostersByUserId, PosterDTO, deletePoster, getPosterById } from '../../api/poster/posterApi';
import { likePoster, unlikePoster, getTotalLikes, checkUserLikedPoster, setUserLikedPoster } from '../../api/poster/likeApi';
import { getCommentsByPosterId, formatCommentTime, countTotalComments, createComment, replyToComment, updateComment, deleteComment, type Comment } from '../../api/poster/commentApi';
import { getUserById } from '../../api/user/userApi';
import { getFriendsList, sendFriendRequest } from '../../api/user/friendshipApi';
import { getSharesByUser, SharePosterDTO, deleteShare, likeShare, unlikeShare, getLikeCountShare, getShareComments, createShareComment, replyToShareComment, updateShareComment, deleteShareComment, checkIfUserLikedShare } from '../../api/poster/shareApi';
import ImageViewer from '../../components/ImageViewer';
import "./ProfileDetail.css";
import "../TrangChu/Home.css"; // Import Home.css for post styles
import Header from "../header-footer/Header";

interface UserDetail {
  idUser: string;
  username: string;
  firstName: string;
  lastName: string;
  email: string;
  phoneNumber: string;
  dateOfBirth: string;
  avatar: string;
  status: boolean;
  enabled: boolean;
  gender: boolean;
  createdAt: string;
  updatedAt: string;
}

interface UpdateProfileRequest {
  idUser: string;
  firstName: string;
  lastName: string;
  phoneNumber: string;
  dateOfBirth: string;
  gender: boolean;
}

interface Post {
  id: string;
  authorId: string;
  authorName: string;
  authorAvatar: string;
  time: string;
  audience: 'public' | 'friends' | 'private';
  content: string;
  images?: string[];
  videos?: Array<{ url: string; thumbnailUrl?: string }>;
  reactions: number;
  comments: number;
  shares: number;
  // Share-specific fields
  isShare?: boolean;
  shareId?: string;
  shareContent?: string;
  shareUserId?: string;
  shareUserName?: string;
  shareUserAvatar?: string;
  shareCreatedAt?: string;
  sharePrivacy?: 'public' | 'friends' | 'private';
  // Original poster fields for shares
  originalPosterId?: string;
  originalAuthorName?: string;
  originalAuthorAvatar?: string;
  originalContent?: string;
  originalImages?: string[];
  originalVideos?: Array<{ url: string; thumbnailUrl?: string }>;
}

const ProfileDetail: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [user, setUser] = useState<UserDetail | null>(null);
  const [loading, setLoading] = useState(true);
  const [isEditing, setIsEditing] = useState(false);
  const [editData, setEditData] = useState<UpdateProfileRequest>({
    idUser: '',
    firstName: '',
    lastName: '',
    phoneNumber: '',
    dateOfBirth: '',
    gender: true,
  });
  const [updateLoading, setUpdateLoading] = useState(false);
  const [message, setMessage] = useState({ type: '', text: '' });
  const [isOwnProfile, setIsOwnProfile] = useState(false);
  // Avatar change states
  const [isChangingAvatar, setIsChangingAvatar] = useState(false);
  const [avatarPreview, setAvatarPreview] = useState<string | null>(null);
  const [avatarFile, setAvatarFile] = useState<File | null>(null);
  const [avatarUploading, setAvatarUploading] = useState(false);
  const fileInputRef = useRef<HTMLInputElement | null>(null);
  const [fieldErrors, setFieldErrors] = useState<Record<string,string>>({});
  const [toasts, setToasts] = useState<Array<{id:number; type:'success'|'error'; text:string}>>([]);
  const toastIdRef = useRef(0);

  // Posts for profile (both posters and shares)
  const [posts, setPosts] = useState<Post[]>([]);
  const [postersLoading, setPostersLoading] = useState(true);
  const [posterPage, setPosterPage] = useState(0);
  const [hasMorePosters, setHasMorePosters] = useState(true);
  const [loadingMorePosters, setLoadingMorePosters] = useState(false);
  const posterObserverRef = useRef<IntersectionObserver | null>(null);
  const lastPosterRef = useRef<HTMLDivElement | null>(null);
  
  // Image viewer state
  const [viewerOpen, setViewerOpen] = useState(false);
  const [viewerImages, setViewerImages] = useState<string[]>([]);
  const [viewerIndex, setViewerIndex] = useState(0);
  
  // Like state
  const [likeCounts, setLikeCounts] = useState<Record<string, number>>({});
  const [userLikedPosts, setUserLikedPosts] = useState<Record<string, boolean>>({});
  const [likingInProgress, setLikingInProgress] = useState<Record<string, boolean>>({});
  
  // Comment state
  const [comments, setComments] = useState<Record<string, Comment[]>>({});
  const [commentCounts, setCommentCounts] = useState<Record<string, number>>({});
  const [showComments, setShowComments] = useState<Record<string, boolean>>({});
  const [loadingComments, setLoadingComments] = useState<Record<string, boolean>>({});
  const [commentInputs, setCommentInputs] = useState<Record<string, string>>({});
  const [submittingComment, setSubmittingComment] = useState<Record<string, boolean>>({});
  
  // Reply state
  const [replyingTo, setReplyingTo] = useState<Record<string, string>>({}); // commentId -> postId
  const [replyInputs, setReplyInputs] = useState<Record<string, string>>({}); // commentId -> content
  const [submittingReply, setSubmittingReply] = useState<Record<string, boolean>>({}); // commentId -> loading
  
  // Edit state
  const [editingComment, setEditingComment] = useState<Record<string, string>>({}); // commentId -> postId
  const [editInputs, setEditInputs] = useState<Record<string, string>>({}); // commentId -> content
  const [submittingEdit, setSubmittingEdit] = useState<Record<string, boolean>>({}); // commentId -> loading
  
  // Nested replies expand/collapse state
  const [expandedReplies, setExpandedReplies] = useState<Record<string, boolean>>({}); // commentId -> expanded
  
  // Post menu state
  const [showPostMenu, setShowPostMenu] = useState<Record<string, boolean>>({}); // postId -> boolean
  
  // Friendship state
  const [isFriend, setIsFriend] = useState(false);
  const [friendshipLoading, setFriendshipLoading] = useState(false);
  const [sendingFriendRequest, setSendingFriendRequest] = useState(false);
  
  // Delete/Edit state
  const [deletingPost, setDeletingPost] = useState<Record<string, boolean>>({});
  
  const currentUserRef = useRef<any>(null);

  useEffect(() => {
    const fetchUser = async () => {
      if (!id) return;
      try {
        const res = await axios.get<UserDetail>(
          `https://unpessimistically-unbewailed-christy.ngrok-free.dev/api/users/search/findByIdUser?IdUser=${id}`
        );
        setUser(res.data);
        
        // Check if this is current user's profile (no login required for viewing)
        const currentUser = getUserInfo(); // May return null if not logged in
        const isOwn = currentUser?.id === id;
        setIsOwnProfile(isOwn);
        
        // Initialize edit data only for own profile
        if (isOwn) {
          setEditData({
            idUser: res.data.idUser,
            firstName: res.data.firstName || '',
            lastName: res.data.lastName || '',
            phoneNumber: res.data.phoneNumber || '',
            dateOfBirth: formatDateForAPI(res.data.dateOfBirth || ''),
            gender: res.data.gender,
          });
        }
      } catch (error) {
        console.error("Li khi ti thng tin user:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchUser();
  }, [id]);

  // Check friendship status
  useEffect(() => {
    const checkFriendship = async () => {
      if (!id || isOwnProfile) {
        setIsFriend(false);
        return;
      }
      
      const currentUser = getUserInfo();
      if (!currentUser?.id) {
        setIsFriend(false);
        return;
      }
      
      setFriendshipLoading(true);
      try {
        const friendsList = await getFriendsList();
        const isAlreadyFriend = friendsList.some(friend => friend.userId === id);
        setIsFriend(isAlreadyFriend);
      } catch (error) {
        console.error('Error checking friendship:', error);
        setIsFriend(false);
      } finally {
        setFriendshipLoading(false);
      }
    };
    
    checkFriendship();
  }, [id, isOwnProfile]);

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString("vi-VN", {
      year: "numeric",
      month: "long",
      day: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    });
  };

  const formatDateForAPI = (dateString: string): string => {
    if (!dateString) return '';
    try {
      const date = new Date(dateString);
      // Ensure we get YYYY-MM-DD format
      return date.toISOString().split('T')[0];
    } catch {
      return '';
    }
  };

  const formatDateForInput = (dateString: string) => {
    if (!dateString) return '';
    try {
      const date = new Date(dateString);
      return date.toISOString().split('T')[0];
    } catch {
      return '';
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setEditData(prev => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleGenderChange = (gender: boolean) => {
    setEditData(prev => ({
      ...prev,
      gender,
    }));
  };

  const handleEdit = () => {
    setIsEditing(true);
    setMessage({ type: '', text: '' });
  };

  const handleCancel = () => {
    setIsEditing(false);
    setMessage({ type: '', text: '' });
    // Reset edit data
    if (user) {
      setEditData({
        idUser: user.idUser,
        firstName: user.firstName || '',
        lastName: user.lastName || '',
        phoneNumber: user.phoneNumber || '',
        dateOfBirth: formatDateForAPI(user.dateOfBirth || ''),
        gender: user.gender,
      });
    }
  };

  // Avatar handlers
  const triggerAvatarSelect = () => {
    if (!isOwnProfile) return;
    fileInputRef.current?.click();
  };

  const handleAvatarFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;
    setFieldErrors({});
    // Validation
    const validTypes = ['image/png', 'image/jpeg', 'image/jpg', 'image/gif', 'image/webp'];
    if (!validTypes.includes(file.type)) {
      setMessage({ type: 'error', text: 'nh dng nh khng hp l. Ch chp nhn PNG, JPG, GIF, WEBP.' });
      return;
    }
    const maxSizeMB = 2;
    if (file.size > maxSizeMB * 1024 * 1024) {
      setMessage({ type: 'error', text: `Kch thc nh qu ln (> ${maxSizeMB}MB).` });
      return;
    }
    setAvatarFile(file);
    const reader = new FileReader();
    reader.onload = () => {
      setAvatarPreview(reader.result as string);
      setIsChangingAvatar(true);
      setMessage({ type: '', text: '' });
    };
    reader.readAsDataURL(file);
  };

  const handleCancelAvatar = () => {
    setIsChangingAvatar(false);
    setAvatarFile(null);
    setAvatarPreview(null);
  };

  const handleUploadAvatar = async () => {
    if (!avatarFile || !user) return;
    try {
      setAvatarUploading(true);
      setMessage({ type: '', text: '' });
      // avatarPreview is already a data URL
      const dataUrl = avatarPreview!;
      const res = await changeAvatar(user.idUser, dataUrl);
      if (res.success) {
        setUser(prev => prev ? { ...prev, avatar: res.avatar || dataUrl } : prev);
        pushToast('success','Cp nht avatar thnh cng!');
        handleCancelAvatar();
      } else {
        pushToast('error', res.message || 'i avatar tht bi');
        if (res.message.includes('401') || res.message.includes('Unauthorized')) {
          setTimeout(() => {
            if (window.confirm('Phin ng nhp  ht hn. Bn c mun ng nhp li khng?')) {
              window.location.href = '/login';
            }
          }, 300);
        }
      }
    } catch (e: any) {
      setMessage({ type: 'error', text: e.message || 'i avatar tht bi' });
    } finally {
      setAvatarUploading(false);
    }
  };

  const handleSave = async () => {
    setUpdateLoading(true);
    setMessage({ type: '', text: '' });
    setFieldErrors({});

    try {
      // Basic validations
      const errors: Record<string,string> = {};
      if (!editData.firstName.trim()) errors.firstName = 'H khng c  trng';
      if (!editData.lastName.trim()) errors.lastName = 'Tn khng c  trng';
      const phonePattern = /^(0|\+84)[3-9][0-9]{8}$/;
      if (editData.phoneNumber && !phonePattern.test(editData.phoneNumber)) errors.phoneNumber = 'S in thoi khng hp l';
      const dob = formatDateForAPI(editData.dateOfBirth);
      if (dob && !/^\d{4}-\d{2}-\d{2}$/.test(dob)) errors.dateOfBirth = 'Ngy sinh khng hp l';
      if (Object.keys(errors).length) {
        setFieldErrors(errors);
        throw new Error('Vui lng sa cc li c nh du');
      }

      const payload: UpdateProfilePayload = {
        idUser: editData.idUser,
        firstName: editData.firstName.trim(),
        lastName: editData.lastName.trim(),
        phoneNumber: editData.phoneNumber.trim(),
        dateOfBirth: dob,
        gender: editData.gender
      };

      console.log('Sending data to API (PUT):', payload);
      const result = await updateUserProfile(payload);
      if (!result.success) throw new Error(result.message);

      pushToast('success','Cp nht thng tin thnh cng!');
      setIsEditing(false);
      setUser(prev => prev ? { ...prev, ...payload } : null);
    } catch (error: any) {
      console.error('Update profile error:', error);
      setMessage({ 
        type: 'error', 
        text: error.message || error.response?.data?.message || 'Cp nht thng tin tht bi' 
      });
      if (!fieldErrors || Object.keys(fieldErrors).length===0) {
        // nu li tng qut -> toast
        pushToast('error', error.message || 'Cp nht tht bi');
      }
    } finally {
      setUpdateLoading(false);
    }
  };

  // Toast helpers
  const pushToast = (type:'success'|'error', text:string) => {
    const id = ++toastIdRef.current;
    setToasts(prev => [...prev,{id,type,text}]);
    setTimeout(()=>dismissToast(id), 3800);
  };
  const dismissToast = (id:number) => {
    setToasts(prev => prev.filter(t=>t.id!==id));
  };

  const fullName = useMemo(() => {
    if (!user) return "";
    const merged = `${user.firstName || ""} ${user.lastName || ""}`.trim();
    return merged || user.username;
  }, [user]);

  const aboutItems = useMemo(
    () => {
      if (!user) return [];
      
      // Base info visible to everyone
      const publicInfo = [
        { icon: UserIcon, label: "H v tn", value: fullName || "Cha cp nht" },
        { icon: Hash, label: "Tn ngi dng", value: user.username ? `@${user.username}` : "Cha cp nht" },
        { icon: UserIcon, label: "Gii tnh", value: user.gender ? "Nam" : "N" },
      ];

      // Additional private info only for own profile
      if (isOwnProfile) {
        publicInfo.push(
          { icon: Mail, label: "Email", value: user.email || "Cha cp nht" },
          { icon: Phone, label: "in thoi", value: user.phoneNumber || "Cha cp nht" },
          { icon: Calendar, label: "Ngy sinh", value: user.dateOfBirth || "Cha cp nht" },
          { icon: ShieldCheck, label: "Ti khon", value: user.status ? " xc thc" : "Cha xc thc" },
          { icon: Clock, label: "Ngy to", value: formatDate(user.createdAt) },
          { icon: Clock, label: "Cp nht cui", value: formatDate(user.updatedAt) }
        );
      }

      return publicInfo;
    },
    [user, fullName, isOwnProfile]
  );

  const friendCount = useMemo(() => Math.max(0, Math.round(Math.random() * 200 + 150)), []);
  const mutualFriends = useMemo(() => Math.max(4, Math.round(Math.random() * 20)), []);

  // Handler: Send friend request
  const handleSendFriendRequest = async () => {
    if (!id) return;
    
    const currentUser = getUserInfo();
    if (!currentUser?.id) {
      alert('Vui lng ng nhp  thm bn b');
      return;
    }

    setSendingFriendRequest(true);
    try {
      const success = await sendFriendRequest(id);
      if (success) {
        pushToast('success', ' gi li mi kt bn!');
        // Optionally: could set some state to show " gi li mi" instead of button
      } else {
        pushToast('error', 'Khng th gi li mi kt bn');
      }
    } catch (error) {
      console.error('Error sending friend request:', error);
      pushToast('error', 'C li xy ra khi gi li mi');
    } finally {
      setSendingFriendRequest(false);
    }
  };

  // Convert PosterDTO to Post
  const convertPosterToPost = useCallback((poster: PosterDTO): Post => {
    const now = new Date();
    const createdDate = new Date(poster.createdAt);
    const diffInMinutes = Math.floor((now.getTime() - createdDate.getTime()) / (1000 * 60));

    let timeStr = '';
    if (diffInMinutes < 1) {
      timeStr = 'Va xong';
    } else if (diffInMinutes < 60) {
      timeStr = `${diffInMinutes} pht trc`;
    } else if (diffInMinutes < 1440) {
      timeStr = `${Math.floor(diffInMinutes / 60)} gi trc`;
    } else {
      timeStr = `${Math.floor(diffInMinutes / 1440)} ngy trc`;
    }

    let audience: 'public' | 'friends' | 'private' = 'public';
    if (poster.privacyStatusName === 'PUBLIC') {
      audience = 'public';
    } else if (poster.privacyStatusName === 'FRIENDS') {
      audience = 'friends';
    } else if (poster.privacyStatusName === 'PRIVATE') {
      audience = 'private';
    }

    return {
      id: poster.idPoster,
      authorId: poster.idUser,
      authorName: poster.userName,
      authorAvatar: poster.userAvatar || 'https://images.unsplash.com/photo-1494790108377-be9c29b29330?auto=format&fit=crop&w=120&q=80',
      time: timeStr,
      audience,
      content: poster.content,
      images: poster.imageUrls,
      videos: poster.videos,
      reactions: 0,
      comments: 0,
      shares: 0,
      isShare: false
    };
  }, []);

  // Convert SharePosterDTO to Post
  const convertShareToPost = useCallback((share: SharePosterDTO): Post => {
    const now = new Date();
    const createdDate = new Date(share.createdAt);
    const diffInMinutes = Math.floor((now.getTime() - createdDate.getTime()) / (1000 * 60));

    let timeStr = '';
    if (diffInMinutes < 1) {
      timeStr = 'Va xong';
    } else if (diffInMinutes < 60) {
      timeStr = `${diffInMinutes} pht trc`;
    } else if (diffInMinutes < 1440) {
      timeStr = `${Math.floor(diffInMinutes / 60)} gi trc`;
    } else {
      timeStr = `${Math.floor(diffInMinutes / 1440)} ngy trc`;
    }

    let audience: 'public' | 'friends' | 'private' = 'public';
    if (share.privacyStatusName === 'PUBLIC') {
      audience = 'public';
    } else if (share.privacyStatusName === 'FRIENDS') {
      audience = 'friends';
    } else if (share.privacyStatusName === 'PRIVATE') {
      audience = 'private';
    }

    return {
      id: share.idShare,
      authorId: share.idUser,
      authorName: share.userName,
      authorAvatar: share.userAvatar || 'https://images.unsplash.com/photo-1494790108377-be9c29b29330?auto=format&fit=crop&w=120&q=80',
      time: timeStr,
      audience,
      content: share.content,
      reactions: share.likeCount || 0,
      comments: share.commentCount || 0,
      shares: 0,
      isShare: true,
      shareId: share.idShare,
      shareContent: share.content,
      shareUserId: share.idUser,
      shareUserName: share.userName,
      shareUserAvatar: share.userAvatar,
      shareCreatedAt: share.createdAt,
      sharePrivacy: audience,
      originalPosterId: share.originalPoster.idPoster,
      originalAuthorName: share.originalPoster.userName,
      originalAuthorAvatar: share.originalPoster.userAvatar,
      originalContent: share.originalPoster.content,
      originalImages: share.originalPoster.images,
      originalVideos: share.originalPoster.videos
    };
  }, []);

  // Helper function to recursively convert ShareCommentDTO to Comment
  const convertShareCommentToComment = useCallback((sc: any, postId: string): Comment => {
    const convert = (item: any): Comment => ({
      idComment: item.idCommentShare,
      idPoster: postId,
      content: item.content,
      idUser: item.idUser,
      parentCommentId: item.parentCommentId || null,
      createdAt: item.createdAt,
      updatedAt: item.updatedAt || item.createdAt,
      userName: item.userName,
      userAvatar: item.userAvatar,
      replyCount: item.replyCount || (item.replies ? item.replies.length : 0),
      // Recursively convert nested replies
      replies: item.replies ? item.replies.map((r: any) => convert(r)) : []
    });
    return convert(sc);
  }, []);

  // Fetch posts (posters + shares) for this profile
  const fetchPosters = useCallback(async (pageNum: number = 0) => {
    if (!id) return;
      
      if (pageNum === 0) {
        setPostersLoading(true);
      } else {
        setLoadingMorePosters(true);
      }
      
      try {
        const currentUser = getUserInfo();
        currentUserRef.current = currentUser;
        
        // Fetch both posters and shares
        const [postersRes, sharesRes] = await Promise.all([
          getPostersByUserId(id),
          getSharesByUser(id)
        ]);
        
        // Filter posters based on privacy if not own profile
        let filteredPosters = postersRes || [];
        let filteredShares = sharesRes || [];
        
        if (!isOwnProfile) {
          // Check if current user is friends with profile owner
          let isFriend = false;
          if (currentUser?.id) {
            try {
              const friendsList = await getFriendsList();
              isFriend = friendsList.some(friend => friend.userId === id);
            } catch (error) {
              console.error('Error checking friendship:', error);
            }
          }
          
          // Filter based on friendship status
          if (isFriend) {
            // Friends can see PUBLIC and FRIENDS posts
            filteredPosters = filteredPosters.filter(p => 
              p.privacyStatusName === 'PUBLIC' || p.privacyStatusName === 'FRIENDS'
            );
            filteredShares = filteredShares.filter(s =>
              s.privacyStatusName === 'PUBLIC' || s.privacyStatusName === 'FRIENDS'
            );
          } else {
            // Non-friends only see PUBLIC posts
            filteredPosters = filteredPosters.filter(p => p.privacyStatusName === 'PUBLIC');
            filteredShares = filteredShares.filter(s => s.privacyStatusName === 'PUBLIC');
          }
        }
        
        // Convert to Post format
        const posterPosts = filteredPosters.map(p => convertPosterToPost(p));
        const sharePosts = await Promise.all(
          filteredShares.map(async (share) => {
            // Enrich share data
            const userInfo = await getUserById(share.idUser);
            const originalPoster = await getPosterById(share.originalPoster.idPoster);
            
            const authorFullName = userInfo 
              ? (`${userInfo.firstName || ''} ${userInfo.lastName || ''}`.trim() 
                 || userInfo.username || share.userName)
              : share.userName;
              
            const originalAuthorFullName = originalPoster?.userName || share.originalPoster.userName;
            
            const post = convertShareToPost(share);
            return {
              ...post,
              authorName: authorFullName,
              originalAuthorName: originalAuthorFullName,
              originalImages: originalPoster?.imageUrls || [],
              originalVideos: originalPoster?.videos || []
            } as Post;
          })
        );
        
        // Merge and sort by date
        const allPosts = [...posterPosts, ...sharePosts].sort((a, b) => {
          const getTimestamp = (post: Post) => {
            if (post.isShare && post.shareCreatedAt) {
              return new Date(post.shareCreatedAt).getTime();
            }
            // Extract timestamp from time string
            const timeStr = post.time;
            if (timeStr === 'Va xong') return Date.now();
            const match = timeStr.match(/(\d+)\s+(pht|gi|ngy)/);
            if (!match) return 0;
            const value = parseInt(match[1]);
            const unit = match[2];
            const now = Date.now();
            if (unit === 'pht') return now - value * 60 * 1000;
            if (unit === 'gi') return now - value * 60 * 60 * 1000;
            if (unit === 'ngy') return now - value * 24 * 60 * 60 * 1000;
            return 0;
          };
          return getTimestamp(b) - getTimestamp(a);
        });
        
        // Paginate
        const PAGE_SIZE = 5;
        const start = pageNum * PAGE_SIZE;
        const end = start + PAGE_SIZE;
        const paginatedPosts = allPosts.slice(start, end);
        
        if (paginatedPosts.length < PAGE_SIZE) {
          setHasMorePosters(false);
        }
        
        if (pageNum === 0) {
          setPosts(paginatedPosts);
        } else {
          setPosts(prev => [...prev, ...paginatedPosts]);
        }
        
        // Fetch like counts for paginated posts
        const likeCountsData: Record<string, number> = {};
        const userLikedData: Record<string, boolean> = {};
        
        await Promise.all(
          paginatedPosts.map(async (post) => {
            try {
              if (post.isShare) {
                // Use share API
                const result = await getLikeCountShare(post.id);
                const count = typeof result === 'number' ? result : ((result as any).likeCount || 0);
                likeCountsData[post.id] = count;
                
                // Check if user liked share
                if (currentUser?.id) {
                  const likedResult = await checkIfUserLikedShare(post.id, currentUser.id);
                  userLikedData[post.id] = likedResult.isLiked || false;
                } else {
                  userLikedData[post.id] = false;
                }
              } else {
                // Use poster API
                const count = await getTotalLikes(post.id);
                likeCountsData[post.id] = count;
                
                if (currentUser?.id) {
                  userLikedData[post.id] = checkUserLikedPoster(post.id, currentUser.id);
                } else {
                  userLikedData[post.id] = false;
                }
              }
            } catch (error) {
              console.error(` Error fetching likes for post ${post.id}:`, error);
              likeCountsData[post.id] = 0;
              userLikedData[post.id] = false;
            }
          })
        );
        
        if (pageNum === 0) {
          setLikeCounts(likeCountsData);
          setUserLikedPosts(userLikedData);
        } else {
          setLikeCounts(prev => ({ ...prev, ...likeCountsData }));
          setUserLikedPosts(prev => ({ ...prev, ...userLikedData }));
        }
        
        // Fetch comment counts for paginated posts
        const commentCountsData: Record<string, number> = {};
        await Promise.all(
          paginatedPosts.map(async (post) => {
            try {
              if (post.isShare) {
                // Use share comments and count recursively
                const shareComments = await getShareComments(post.id);
                const totalCount = countTotalComments(shareComments.map((sc: any) => convertShareCommentToComment(sc, post.id)));
                commentCountsData[post.id] = totalCount;
              } else {
                // Use poster comments
                const postComments = await getCommentsByPosterId(post.id);
                const totalCount = countTotalComments(postComments);
                commentCountsData[post.id] = totalCount;
              }
            } catch (error) {
              console.error(` Error fetching comments for post ${post.id}:`, error);
              commentCountsData[post.id] = 0;
            }
          })
        );
        
        if (pageNum === 0) {
          setCommentCounts(commentCountsData);
        } else {
          setCommentCounts(prev => ({ ...prev, ...commentCountsData }));
        }
      } catch (e) {
        console.error('Error fetching posts for profile:', e);
        setHasMorePosters(false);
      } finally {
        if (pageNum === 0) {
          setPostersLoading(false);
        } else {
          setLoadingMorePosters(false);
        }
      }
  }, [id, isOwnProfile, convertPosterToPost, convertShareToPost, convertShareCommentToComment]);

  // Initial load of posters
  useEffect(() => {
    fetchPosters(0);
    setPosterPage(0);
  }, [id, fetchPosters]);

  // Intersection Observer for infinite scroll (posters)
  useEffect(() => {
    // Cleanup previous observer
    if (posterObserverRef.current) {
      posterObserverRef.current.disconnect();
    }

    // Create new observer
    posterObserverRef.current = new IntersectionObserver(
      (entries) => {
        const firstEntry = entries[0];
        if (firstEntry.isIntersecting && hasMorePosters && !loadingMorePosters && !postersLoading) {
          console.log(' Loading more posters...');
          const nextPage = posterPage + 1;
          setPosterPage(nextPage);
          fetchPosters(nextPage);
        }
      },
      { threshold: 0.5 }
    );

    // Observe last poster element
    if (lastPosterRef.current) {
      posterObserverRef.current.observe(lastPosterRef.current);
    }

    return () => {
      if (posterObserverRef.current) {
        posterObserverRef.current.disconnect();
      }
    };
  }, [hasMorePosters, loadingMorePosters, postersLoading, posterPage, fetchPosters]);

  // Handler functions - copied from Home.tsx
  const handleDeletePost = async (postId: string, authorId: string, isShare: boolean = false) => {
    if (!window.confirm('Bn c chc chn mun xa bi ng ny?')) {
      return;
    }

    setDeletingPost(prev => ({ ...prev, [postId]: true }));
    try {
      if (isShare) {
        await deleteShare(postId, authorId);
      } else {
        await deletePoster(postId, authorId);
      }
      // Remove from local state
      setPosts(prev => prev.filter(p => p.id !== postId));
      console.log(' Post deleted successfully');
    } catch (err: any) {
      console.error('Error deleting post:', err);
      alert(err.response?.data?.message || 'Khng th xa bi ng');
    } finally {
      setDeletingPost(prev => ({ ...prev, [postId]: false }));
    }
  };

  const handleLikeToggle = async (postId: string) => {
    const currentUser = currentUserRef.current;
    if (!currentUser?.id) {
      alert('Vui lng ng nhp  thch bi vit');
      return;
    }

    if (likingInProgress[postId]) {
      return;
    }

    // Find the post to check if it's a share
    const post = posts.find(p => p.id === postId);
    const isShare = post?.isShare || false;

    const isCurrentlyLiked = userLikedPosts[postId] || false;
    const currentCount = likeCounts[postId] || 0;

    // Optimistic update
    setUserLikedPosts(prev => ({ ...prev, [postId]: !isCurrentlyLiked }));
    setLikeCounts(prev => ({ 
      ...prev, 
      [postId]: isCurrentlyLiked ? Math.max(0, currentCount - 1) : currentCount + 1 
    }));
    setLikingInProgress(prev => ({ ...prev, [postId]: true }));

    try {
      if (isShare) {
        // Use Share API
        if (isCurrentlyLiked) {
          await unlikeShare(postId, currentUser.id);
        } else {
          const result = await likeShare(postId, { userId: currentUser.id });
          const newCount = typeof result === 'number' ? result : ((result as any).likeCount || 0);
          setLikeCounts(prev => ({ ...prev, [postId]: newCount }));
        }
      } else {
        // Use Poster API
        let success = false;
        if (isCurrentlyLiked) {
          success = await unlikePoster(postId, currentUser.id);
          if (success) {
            setUserLikedPoster(postId, currentUser.id, false);
            // Confirm the state change
            setUserLikedPosts(prev => ({ ...prev, [postId]: false }));
          }
        } else {
          success = await likePoster(postId, currentUser.id);
          if (success) {
            setUserLikedPoster(postId, currentUser.id, true);
            // Confirm the state change
            setUserLikedPosts(prev => ({ ...prev, [postId]: true }));
          }
        }

        if (!success) {
          // Rollback on failure
          setUserLikedPosts(prev => ({ ...prev, [postId]: isCurrentlyLiked }));
          setLikeCounts(prev => ({ ...prev, [postId]: currentCount }));
        } else {
          // Update count from server
          const newCount = await getTotalLikes(postId);
          setLikeCounts(prev => ({ ...prev, [postId]: newCount }));
        }
      }
    } catch (error) {
      console.error(' Error toggling like:', error);
      setUserLikedPosts(prev => ({ ...prev, [postId]: isCurrentlyLiked }));
      setLikeCounts(prev => ({ ...prev, [postId]: currentCount }));
    } finally {
      setLikingInProgress(prev => ({ ...prev, [postId]: false }));
    }
  };

  const handleToggleComments = async (postId: string) => {
    const isCurrentlyShown = showComments[postId] || false;
    
    if (isCurrentlyShown) {
      setShowComments(prev => ({ ...prev, [postId]: false }));
    } else {
      if (!comments[postId]) {
        setLoadingComments(prev => ({ ...prev, [postId]: true }));
        try {
          // Find the post to check if it's a share
          const post = posts.find(p => p.id === postId);
          const isShare = post?.isShare || false;
          
          let postComments: Comment[];
          if (isShare) {
            // Use Share API
            const shareComments = await getShareComments(postId);
            // Convert ShareCommentDTO[] to Comment[] recursively
            postComments = shareComments.map(sc => convertShareCommentToComment(sc, postId));
          } else {
            // Use Poster API
            postComments = await getCommentsByPosterId(postId);
          }
          
          const enrichedComments = await enrichCommentsWithUserData(postComments);
          setComments(prev => ({ ...prev, [postId]: enrichedComments }));
        } catch (error) {
          console.error(' Error loading comments:', error);
        } finally {
          setLoadingComments(prev => ({ ...prev, [postId]: false }));
        }
      }
      setShowComments(prev => ({ ...prev, [postId]: true }));
    }
  };

  const enrichCommentsWithUserData = async (commentList: Comment[]): Promise<Comment[]> => {
    const userCache: Record<string, any> = {};
    
    const enrichComment = async (comment: Comment): Promise<Comment> => {
      if (!userCache[comment.idUser]) {
        try {
          const userData = await getUserById(comment.idUser);
          userCache[comment.idUser] = userData;
        } catch (error) {
          console.error(`Error fetching user ${comment.idUser}:`, error);
          userCache[comment.idUser] = null;
        }
      }
      
      const user = userCache[comment.idUser];
      const enrichedComment = {
        ...comment,
        userName: user?.username || 'Ngi dng',
        userAvatar: user?.avatar || '',
        userFirstName: user?.firstName || '',
        userLastName: user?.lastName || ''
      };
      
      if (comment.replies && comment.replies.length > 0) {
        enrichedComment.replies = await Promise.all(
          comment.replies.map(reply => enrichComment(reply))
        );
      }
      
      return enrichedComment;
    };
    
    return Promise.all(commentList.map(enrichComment));
  };

  const handleSubmitComment = async (postId: string) => {
    const currentUser = currentUserRef.current;
    if (!currentUser?.id) {
      alert('Vui lng ng nhp  bnh lun');
      return;
    }

    const content = commentInputs[postId]?.trim();
    if (!content) return;

    // Find the post to check if it's a share
    const post = posts.find(p => p.id === postId);
    const isShare = post?.isShare || false;

    setSubmittingComment(prev => ({ ...prev, [postId]: true }));

    try {
      let newComment;
      
      if (isShare) {
        // Use Share API
        const shareComment = await createShareComment(postId, {
          userId: currentUser.id,
          content: content
        });
        
        // Convert ShareCommentDTO to Comment format
        newComment = {
          idComment: shareComment.idCommentShare,
          idPoster: postId,
          content: shareComment.content,
          idUser: shareComment.idUser,
          parentCommentId: shareComment.parentCommentId || null,
          createdAt: shareComment.createdAt,
          updatedAt: shareComment.updatedAt || shareComment.createdAt,
          replies: [],
          replyCount: 0
        };
      } else {
        // Use Poster API
        newComment = await createComment(postId, currentUser.id, content);
      }
      
      if (newComment) {
        const userData = await getUserById(currentUser.id);
        const enrichedComment: Comment = {
          ...newComment,
          userName: userData?.username || currentUser.username || 'Ngi dng',
          userAvatar: userData?.avatar || currentUser.avatar || '',
          userFirstName: userData?.firstName || currentUser.firstName || '',
          userLastName: userData?.lastName || currentUser.lastName || '',
          replies: newComment.replies || [],
          replyCount: newComment.replyCount || 0
        };

        setComments(prev => ({
          ...prev,
          [postId]: [enrichedComment, ...(prev[postId] || [])]
        }));

        setCommentCounts(prev => ({
          ...prev,
          [postId]: (prev[postId] || 0) + 1
        }));

        setCommentInputs(prev => ({ ...prev, [postId]: '' }));
        setShowComments(prev => ({ ...prev, [postId]: true }));
      }
    } catch (error) {
      console.error(' Error submitting comment:', error);
      alert('C li xy ra khi thm bnh lun.');
    } finally {
      setSubmittingComment(prev => ({ ...prev, [postId]: false }));
    }
  };

  const handleToggleReply = (commentId: string, postId: string) => {
    setReplyingTo(prev => {
      const current = prev[commentId];
      if (current) {
        const newState = { ...prev };
        delete newState[commentId];
        return newState;
      } else {
        return { ...prev, [commentId]: postId };
      }
    });
  };

  const handleToggleNestedReplies = (commentId: string) => {
    setExpandedReplies(prev => ({ ...prev, [commentId]: !prev[commentId] }));
  };

  const handleSubmitReply = async (postId: string, parentCommentId: string) => {
    const currentUser = currentUserRef.current;
    if (!currentUser?.id) {
      alert('Vui lng ng nhp  tr li bnh lun');
      return;
    }

    const content = replyInputs[parentCommentId]?.trim();
    if (!content) return;

    // Find the post to check if it's a share
    const post = posts.find(p => p.id === postId);
    const isShare = post?.isShare || false;

    setSubmittingReply(prev => ({ ...prev, [parentCommentId]: true }));

    try {
      let newReply;
      
      if (isShare) {
        // Use Share API
        const shareReply = await replyToShareComment(parentCommentId, {
          userId: currentUser.id,
          content: content
        });
        
        // Convert ShareCommentDTO to Comment format
        newReply = {
          idComment: shareReply.idCommentShare,
          idPoster: postId,
          content: shareReply.content,
          idUser: shareReply.idUser,
          parentCommentId: shareReply.parentCommentId || null,
          createdAt: shareReply.createdAt,
          updatedAt: shareReply.updatedAt || shareReply.createdAt,
          replies: [],
          replyCount: 0
        };
      } else {
        // Use Poster API
        newReply = await replyToComment(postId, parentCommentId, currentUser.id, content);
      }
      
      if (newReply) {
        const userData = await getUserById(currentUser.id);
        const enrichedReply: Comment = {
          ...newReply,
          userName: userData?.username || currentUser.username || 'Ngi dng',
          userAvatar: userData?.avatar || currentUser.avatar || '',
          userFirstName: userData?.firstName || currentUser.firstName || '',
          userLastName: userData?.lastName || currentUser.lastName || '',
          replies: newReply.replies || [],
          replyCount: newReply.replyCount || 0
        };

        setComments(prev => {
          const postComments = [...(prev[postId] || [])];
          
          // Find and update the exact parent comment recursively (maintain nested structure)
          const updateCommentReplies = (commentsList: Comment[]): Comment[] => {
            return commentsList.map(comment => {
              if (comment.idComment === parentCommentId) {
                // Found the parent - add reply here
                return {
                  ...comment,
                  replies: [enrichedReply, ...(comment.replies || [])],
                  replyCount: (comment.replyCount || 0) + 1
                };
              } else if (comment.replies && comment.replies.length > 0) {
                // Search in nested replies recursively
                return {
                  ...comment,
                  replies: updateCommentReplies(comment.replies)
                };
              }
              return comment;
            });
          };

          return {
            ...prev,
            [postId]: updateCommentReplies(postComments)
          };
        });
        
        // Auto-expand the parent comment to show the new reply
        setExpandedReplies(prev => ({ ...prev, [parentCommentId]: true }));

        setCommentCounts(prev => ({
          ...prev,
          [postId]: (prev[postId] || 0) + 1
        }));

        setReplyInputs(prev => {
          const newState = { ...prev };
          delete newState[parentCommentId];
          return newState;
        });
        setReplyingTo(prev => {
          const newState = { ...prev };
          delete newState[parentCommentId];
          return newState;
        });
      }
    } catch (error) {
      console.error(' Error submitting reply:', error);
      alert('C li xy ra khi thm phn hi.');
    } finally {
      setSubmittingReply(prev => ({ ...prev, [parentCommentId]: false }));
    }
  };

  const handleToggleEdit = (commentId: string, postId: string, currentContent: string) => {
    setEditingComment(prev => {
      const current = prev[commentId];
      if (current) {
        const newState = { ...prev };
        delete newState[commentId];
        return newState;
      } else {
        setEditInputs(prevInputs => ({ ...prevInputs, [commentId]: currentContent }));
        return { ...prev, [commentId]: postId };
      }
    });
  };

  const handleSubmitEdit = async (postId: string, commentId: string) => {
    const currentUser = currentUserRef.current;
    if (!currentUser?.id) {
      alert('Vui lng ng nhp  sa bnh lun');
      return;
    }

    const content = editInputs[commentId]?.trim();
    if (!content) return;

    // Find the post to check if it's a share
    const post = posts.find(p => p.id === postId);
    const isShare = post?.isShare || false;

    setSubmittingEdit(prev => ({ ...prev, [commentId]: true }));

    try {
      let updatedComment: Comment | null | undefined;
      
      if (isShare) {
        // Use Share API
        const shareComment = await updateShareComment(commentId, {
          userId: currentUser.id,
          content: content
        });
        
        // Convert ShareCommentDTO to Comment format
        updatedComment = {
          idComment: shareComment.idCommentShare,
          idPoster: postId,
          content: shareComment.content,
          idUser: shareComment.idUser,
          parentCommentId: shareComment.parentCommentId || null,
          createdAt: shareComment.createdAt,
          updatedAt: shareComment.updatedAt || shareComment.createdAt,
          replies: [],
          replyCount: 0
        };
      } else {
        // Use Poster API
        updatedComment = await updateComment(postId, commentId, currentUser.id, content);
      }
      
      if (updatedComment) {
        // Store in const to avoid null/undefined issues in nested function
        const newContent = updatedComment.content;
        const newUpdatedAt = updatedComment.updatedAt;
        
        setComments(prev => {
          const postComments = [...(prev[postId] || [])];
          const updateCommentContent = (commentsList: Comment[]): Comment[] => {
            return commentsList.map(comment => {
              if (comment.idComment === commentId) {
                return {
                  ...comment,
                  content: newContent,
                  updatedAt: newUpdatedAt
                };
              } else if (comment.replies && comment.replies.length > 0) {
                return {
                  ...comment,
                  replies: updateCommentContent(comment.replies)
                };
              }
              return comment;
            });
          };

          return {
            ...prev,
            [postId]: updateCommentContent(postComments)
          };
        });

        setEditInputs(prev => {
          const newState = { ...prev };
          delete newState[commentId];
          return newState;
        });
        setEditingComment(prev => {
          const newState = { ...prev };
          delete newState[commentId];
          return newState;
        });
      }
    } catch (error) {
      console.error(' Error updating comment:', error);
      alert('C li xy ra khi cp nht bnh lun.');
    } finally {
      setSubmittingEdit(prev => ({ ...prev, [commentId]: false }));
    }
  };

  const handleDeleteComment = async (postId: string, commentId: string) => {
    const currentUser = currentUserRef.current;
    if (!currentUser?.id) {
      alert('Vui lng ng nhp  xa bnh lun');
      return;
    }

    if (!window.confirm('Bn c chc chn mun xa bnh lun ny?')) {
      return;
    }

    // Find the post to check if it's a share
    const post = posts.find(p => p.id === postId);
    const isShare = post?.isShare || false;

    try {
      let success;
      
      if (isShare) {
        // Use Share API
        await deleteShareComment(commentId, currentUser.id);
        success = true;
      } else {
        // Use Poster API
        success = await deleteComment(postId, commentId, currentUser.id);
      }
      
      if (success) {
        setComments(prev => {
          const postComments = [...(prev[postId] || [])];
          const removeComment = (commentsList: Comment[]): Comment[] => {
            return commentsList.filter(comment => {
              if (comment.idComment === commentId) {
                return false;
              } else if (comment.replies && comment.replies.length > 0) {
                comment.replies = removeComment(comment.replies);
              }
              return true;
            });
          };

          return {
            ...prev,
            [postId]: removeComment(postComments)
          };
        });

        setCommentCounts(prev => ({
          ...prev,
          [postId]: Math.max(0, (prev[postId] || 0) - 1)
        }));
      }
    } catch (error) {
      console.error(' Error deleting comment:', error);
      alert('C li xy ra khi xa bnh lun.');
    }
  };

  // Render nested replies recursively
  const renderReply = (reply: Comment, postId: string, depth: number = 1) => {
    const hasNestedReplies = reply.replies && reply.replies.length > 0;
    const isExpanded = expandedReplies[reply.idComment];
    const maxDepth = 3; // Limit nesting depth for UI
    
    return (
      <div key={reply.idComment} className={`fb-comment__reply-wrapper fb-comment__reply--depth-${depth}`}>
        <div className="fb-comment fb-comment--reply">
        <NavLink to={`/user/${reply.idUser}`} className="fb-post__author">
          <img 
            src={reply.userAvatar || 'https://images.unsplash.com/photo-1494790108377-be9c29b29330?auto=format&fit=crop&w=80&q=80'} 
            alt={`${reply.userFirstName} ${reply.userLastName}`}
            className="fb-comment__avatar"
          />
        </NavLink>
        <div className="fb-comment__content">
          {editingComment[reply.idComment] ? (
            // Edit Mode
            <div className="fb-comment__edit">
              <input
                type="text"
                value={editInputs[reply.idComment] || ''}
                onChange={(e) => setEditInputs(prev => ({ ...prev, [reply.idComment]: e.target.value }))}
                onKeyDown={(e) => {
                  if (e.key === 'Enter' && !e.shiftKey) {
                    e.preventDefault();
                    handleSubmitEdit(postId, reply.idComment);
                  } else if (e.key === 'Escape') {
                    handleToggleEdit(reply.idComment, postId, reply.content);
                  }
                }}
                className="fb-comment__edit-field"
                disabled={submittingEdit[reply.idComment]}
                autoFocus
              />
              <div className="fb-comment__edit-actions">
                <button
                  type="button"
                  onClick={() => handleToggleEdit(reply.idComment, postId, reply.content)}
                  disabled={submittingEdit[reply.idComment]}
                >
                  Hy
                </button>
                <button
                  type="button"
                  onClick={() => handleSubmitEdit(postId, reply.idComment)}
                  disabled={submittingEdit[reply.idComment] || !editInputs[reply.idComment]?.trim()}
                  className="btn-primary"
                >
                  {submittingEdit[reply.idComment] ? 'ang lu...' : 'Lu'}
                </button>
              </div>
            </div>
          ) : (
            // View Mode
            <>
              <div className="fb-comment__bubble">
                <strong>
                  {reply.userFirstName && reply.userLastName 
                    ? `${reply.userFirstName} ${reply.userLastName}`.trim()
                    : reply.userName || 'Ngi dng'}
                </strong>
                <p>{reply.content}</p>
              </div>
              <div className="fb-comment__meta">
                <span>{formatCommentTime(reply.createdAt)}</span>
                <button type="button">Thch</button>
                <button 
                  type="button"
                  onClick={() => handleToggleReply(reply.idComment, postId)}
                >
                  Phn hi
                </button>
                {currentUserRef.current?.id === reply.idUser && (
                  <>
                    <button 
                      type="button"
                      onClick={() => handleToggleEdit(reply.idComment, postId, reply.content)}
                    >
                      Sa
                    </button>
                    <button 
                      type="button"
                      onClick={() => handleDeleteComment(postId, reply.idComment)}
                    >
                      Xa
                    </button>
                  </>
                )}
              </div>
              
              {/* Show nested replies toggle button */}
              {hasNestedReplies && depth < maxDepth && (
                <button 
                  type="button"
                  className="fb-comment__view-replies"
                  onClick={() => handleToggleNestedReplies(reply.idComment)}
                >
                  {isExpanded ? '' : ''} {reply.replyCount || reply.replies!.length} phn hi
                </button>
              )}
            </>
          )}
        </div>
      </div>
        
      {/* Reply Input - Outside fb-comment div, sibling */}
      {replyingTo[reply.idComment] && (
        <div className="fb-reply-input">
          <img 
            src={currentUserRef.current?.avatar || 'https://images.unsplash.com/photo-1494790108377-be9c29b29330?auto=format&fit=crop&w=80&q=80'} 
            alt="Your avatar" 
            className="fb-reply-input__avatar"
          />
          <div className="fb-reply-input__field-wrapper">
            <input
              type="text"
              placeholder="Vit phn hi..."
              value={replyInputs[reply.idComment] || ''}
              onChange={(e) => setReplyInputs(prev => ({ ...prev, [reply.idComment]: e.target.value }))}
              onKeyDown={(e) => {
                if (e.key === 'Enter' && !e.shiftKey) {
                  e.preventDefault();
                  handleSubmitReply(postId, reply.idComment);
                }
              }}
              className="fb-reply-input__field"
              disabled={submittingReply[reply.idComment]}
            />
            {replyInputs[reply.idComment]?.trim() && (
              <button
                type="button"
                onClick={() => handleSubmitReply(postId, reply.idComment)}
                disabled={submittingReply[reply.idComment]}
                className="fb-reply-input__submit"
              >
                {submittingReply[reply.idComment] ? '...' : ''}
              </button>
            )}
          </div>
        </div>
      )}
      
      {/* Nested Replies - Outside fb-comment div, sibling */}
      {hasNestedReplies && isExpanded && depth < maxDepth && (
        <div className="fb-comment__replies">
          {reply.replies!.map(nestedReply => renderReply(nestedReply, postId, depth + 1))}
        </div>
      )}
    </div>
    );
  };

  // Image viewer handlers
  const openImageViewer = (images: string[], index: number) => {
    setViewerImages(images);
    setViewerIndex(index);
    setViewerOpen(true);
  };

  const closeImageViewer = () => {
    setViewerOpen(false);
  };

  const nextImage = () => {
    setViewerIndex(prev => Math.min(prev + 1, viewerImages.length - 1));
  };

  const prevImage = () => {
    setViewerIndex(prev => Math.max(prev - 1, 0));
  };

  // Toggle post menu
  const handleTogglePostMenu = (postId: string) => {
    setShowPostMenu(prev => ({
      ...prev,
      [postId]: !prev[postId]
    }));
  };

  // Close post menu when clicking outside
  const handleClosePostMenu = (postId: string) => {
    setShowPostMenu(prev => ({
      ...prev,
      [postId]: false
    }));
  };

  // Handle edit post
  const handleEditPost = (postId: string) => {
    handleClosePostMenu(postId);
    navigate(`/poster/${postId}/edit`);
  };

  // Handle delete post with menu close
  const handleDeletePostWithMenu = async (postId: string, authorId: string, isShare: boolean = false) => {
    handleClosePostMenu(postId);
    await handleDeletePost(postId, authorId, isShare);
  };

  if (loading) {
    return (
      <div className="loading-container">
        <div>
          <div className="loading-spinner"></div>
          <p className="loading-text"> ang ti thng tin ngi dng...</p>
        </div>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="error-container">
        <div className="error-message">
          <p> Khng tm thy ngi dng</p>
          <a href="/" className="back-link"> Quay li trang ch</a>
        </div>
      </div>
    );
  }

  return (
    <>
      {/* <Header /> */}
      <div className="fb-profile">
        <div className="toast-container" aria-live="polite" aria-atomic="true">
          {toasts.map(t => (
            <div key={t.id} className={`toast toast-${t.type}`} role="alert">
              <div className="toast__text">{t.text}</div>
              <button
                type="button"
                className="toast__close"
                onClick={() => dismissToast(t.id)}
                aria-label="ng thng bo"
              >
                
              </button>
            </div>
          ))}
        </div>

      <div className="fb-profile__cover">
        <div className="fb-profile__cover-image" />
        {isOwnProfile && (
          <button type="button" className="fb-btn fb-btn--cover">
            <ImageIcon size={16} />
            <span>Chnh sa nh ba</span>
          </button>
        )}
      </div>

      <div className="fb-profile__top">
        <div className="fb-profile__avatar-col">
          <div className="avatar-container">
            <img
              src={
                avatarPreview
                  ? avatarPreview
                  : user.avatar && user.avatar.trim() !== ""
                  ? user.avatar
                  : "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQQEjGbsTwEJ2n8tZOeJWLkCivjuYDJBxQbIg&s"
              }
              alt="nh i din"
              className={"avatar" + (isOwnProfile ? " avatar-hover" : "")}
              onClick={triggerAvatarSelect}
              title={isOwnProfile ? "Nhp  i avatar" : ""}
            />
            {isOwnProfile && (
              <div className={`avatar-overlay ${avatarUploading ? "avatar-uploading" : ""}`}>
                {avatarUploading ? <div className="avatar-progress" /> : <span>{isChangingAvatar ? "Lu hoc hu" : "i nh"}</span>}
              </div>
            )}
            {isOwnProfile && !isChangingAvatar && (
              <button type="button" className="avatar-change-btn" onClick={triggerAvatarSelect}>
                i
              </button>
            )}
            {isChangingAvatar && (
              <div className="avatar-actions">
                <button type="button" disabled={avatarUploading} onClick={handleUploadAvatar} className="save-avatar-btn">
                  {avatarUploading ? "ang lu..." : "Lu"}
                </button>
                <button type="button" disabled={avatarUploading} onClick={handleCancelAvatar} className="cancel-avatar-btn">
                  Hy
                </button>
              </div>
            )}
            <input
              type="file"
              ref={fileInputRef}
              accept="image/*"
              className="visually-hidden"
              aria-labelledby="avatar-upload"
              title="Chn nh i din"
              placeholder="Chn nh i din"
              onChange={handleAvatarFileChange}
            />
            <div className={`status-indicator ${user.status ? "status-online" : "status-offline"}`} />
          </div>
        </div>
        <div className="fb-profile__info-col">
          <h1 className="fb-profile__name">{fullName}</h1>
          <div className="fb-profile__meta">
            {/* <span className="fb-profile__username">{user.username}</span> */}
            <span className="fb-profile__friends">
              <Users size={16} />
              <span>{friendCount.toLocaleString("vi-VN")} bn b</span>
              <span className="dot"></span>
              <span>{mutualFriends} bn chung</span>
            </span>
          </div>
          <div className="fb-profile__badges">
            <span className={`fb-badge ${user.enabled ? "fb-badge--success" : "fb-badge--danger"}`}>
              {user.enabled ? "Hot ng" : "Kha"}
            </span>
            <span className="fb-badge fb-badge--muted">{user.gender ? "Nam" : "N"}</span>
          </div>
        </div>
        <div className="fb-profile__actions">
          {isOwnProfile ? (
            <div className="fb-profile__action-group">
              {!isEditing ? (
                <button type="button" className="fb-btn fb-btn--primary" onClick={handleEdit}>
                  <Edit size={16} />
                  <span>Chnh sa trang c nhn</span>
                </button>
              ) : (
                <div className="fb-profile__action-edit">
                  <button type="button" className="fb-btn fb-btn--primary" onClick={handleSave} disabled={updateLoading}>
                    <Save size={16} />
                    <span>{updateLoading ? "ang lu..." : "Lu thay i"}</span>
                  </button>
                  <button type="button" className="fb-btn fb-btn--light" onClick={handleCancel}>
                    <X size={16} />
                    <span>Hy</span>
                  </button>
                </div>
              )}
              <button type="button" className="fb-btn fb-btn--light">
                <Plus size={16} />
                <span>Thm vo tin</span>
              </button>
            </div>
          ) : (
            <div className="fb-profile__action-group">
              {!friendshipLoading && !isFriend && (
                <button 
                  type="button" 
                  className="fb-btn fb-btn--primary"
                  onClick={handleSendFriendRequest}
                  disabled={sendingFriendRequest}
                >
                  <UserPlus size={16} />
                  <span>{sendingFriendRequest ? 'ang gi...' : 'Thm bn b'}</span>
                </button>
              )}
              {!friendshipLoading && isFriend && (
                <button type="button" className="fb-btn fb-btn--secondary">
                  <Users size={16} />
                  <span>Bn b</span>
                </button>
              )}
              <button type="button" className="fb-btn fb-btn--secondary">
                <MessageCircle size={16} />
                <span>Nhn tin</span>
              </button>
              <button type="button" className="fb-btn fb-btn--icon" aria-label="Ty chn khc">
                <MoreHorizontal size={18} />
              </button>
            </div>
          )}
        </div>
      </div>

      <div className="fb-profile__tabs" role="tablist">
        <button type="button" className="fb-profile__tab fb-profile__tab--active" role="tab" aria-selected="true">
          Bi vit
        </button>
        <button type="button" className="fb-profile__tab" role="tab">
          Chi tit v Dng Thun Tri
        </button>
        <button type="button" className="fb-profile__tab" role="tab">
          Bn b
        </button>
        <button type="button" className="fb-profile__tab" role="tab">
          nh
        </button>
        <button type="button" className="fb-profile__tab" role="tab">
          Video
        </button>
        <button type="button" className="fb-profile__tab" role="tab">
          Xem thm
        </button>
      </div>

      <div className="fb-profile__layout">
        <aside className="fb-profile__sidebar">
          {isEditing ? (
            <div className="fb-card fb-profile__edit-card">
              <div className="fb-card__header">
                <h2>Chnh sa thng tin</h2>
              </div>
              <div className="fb-profile__edit-body">
                <div className="form-grid">
                  <div className="form-group">
                    <label className="form-label" htmlFor="profile-first-name">H</label>
                    <input
                      id="profile-first-name"
                      type="text"
                      name="firstName"
                      value={editData.firstName}
                      onChange={handleInputChange}
                      className={`form-input${fieldErrors.firstName ? " error" : ""}`}
                      placeholder="Nhp h ca bn"
                      title="H"
                    />
                    {fieldErrors.firstName && <div className="field-error">{fieldErrors.firstName}</div>}
                  </div>
                  <div className="form-group">
                    <label className="form-label" htmlFor="profile-last-name">Tn</label>
                    <input
                      id="profile-last-name"
                      type="text"
                      name="lastName"
                      value={editData.lastName}
                      onChange={handleInputChange}
                      className={`form-input${fieldErrors.lastName ? " error" : ""}`}
                      placeholder="Nhp tn ca bn"
                      title="Tn"
                    />
                    {fieldErrors.lastName && <div className="field-error">{fieldErrors.lastName}</div>}
                  </div>
                  <div className="form-group">
                    <label className="form-label" htmlFor="profile-phone-number">S in thoi</label>
                    <input
                      id="profile-phone-number"
                      type="tel"
                      name="phoneNumber"
                      value={editData.phoneNumber}
                      onChange={handleInputChange}
                      className={`form-input${fieldErrors.phoneNumber ? " error" : ""}`}
                      placeholder="0909123456"
                      title="S in thoi"
                    />
                    {fieldErrors.phoneNumber && <div className="field-error">{fieldErrors.phoneNumber}</div>}
                  </div>
                  <div className="form-group">
                    <label className="form-label" htmlFor="profile-dob">Ngy sinh</label>
                    <input
                      id="profile-dob"
                      type="date"
                      name="dateOfBirth"
                      value={formatDateForInput(editData.dateOfBirth)}
                      onChange={handleInputChange}
                      className={`form-input${fieldErrors.dateOfBirth ? " error" : ""}`}
                    />
                    {fieldErrors.dateOfBirth && <div className="field-error">{fieldErrors.dateOfBirth}</div>}
                  </div>
                  <div className="form-group">
                    <span className="form-label">Gii tnh</span>
                    <div className="radio-group">
                      <label className="radio-option">
                        <input
                          type="radio"
                          name="gender"
                          checked={editData.gender === true}
                          onChange={() => handleGenderChange(true)}
                        />
                        <span>Nam</span>
                      </label>
                      <label className="radio-option">
                        <input
                          type="radio"
                          name="gender"
                          checked={editData.gender === false}
                          onChange={() => handleGenderChange(false)}
                        />
                        <span>N</span>
                      </label>
                    </div>
                  </div>
                </div>
                <div className="fb-profile__edit-actions">
                  <button type="button" className="fb-btn fb-btn--primary" onClick={handleSave} disabled={updateLoading}>
                    {updateLoading ? "ang lu..." : "Lu thay i"}
                  </button>
                  <button type="button" className="fb-btn fb-btn--light" onClick={handleCancel} disabled={updateLoading}>
                    Hy
                  </button>
                </div>
                {message.text && <div className={`profile-message ${message.type}`}>{message.text}</div>}
              </div>
            </div>
          ) : (
            <div className="fb-card fb-profile__about-card">
              <div className="fb-card__header">
                <h2>Gii thiu</h2>
                {isOwnProfile && (
                  <button type="button" className="fb-link-button" onClick={handleEdit}>
                    Chnh sa
                  </button>
                )}
              </div>
              <ul className="fb-profile__about-list">
                {aboutItems.map(item => (
                  <li key={item.label}>
                    <item.icon size={18} />
                    <div>
                      <span className="label">{item.label}</span>
                      <span className="value">{item.value || "Cha cp nht"}</span>
                    </div>
                  </li>
                ))}
              </ul>
            </div>
          )}
          

          {/* <section className="fb-card fb-profile__photos">
            <div className="fb-card__header">
              <h2>nh</h2>
              <button type="button" className="fb-link-button">Xem tt c</button>
            </div>
            <div className="fb-photos-grid">
              {samplePhotos.map((photo, index) => (
                <img key={index} src={photo} alt={`nh ${index + 1}`} />
              ))}
            </div>
          </section>

          <section className="fb-card fb-profile__friends">
            <div className="fb-card__header">
              <h2>Bn b</h2>
              <span>{friendCount.toLocaleString("vi-VN")} ngi bn</span>
            </div>
            <div className="fb-friends-grid">
              {sampleFriends.map(friend => (
                <div key={friend.id} className="fb-friend">
                  <img src={friend.avatar} alt={friend.name} />
                  <span>{friend.name}</span>
                </div>
              ))}
            </div>
          </section> */}
        </aside>

        <section className="fb-profile__main">
          {isOwnProfile && (
            <div className="fb-card fb-profile__composer">
              <div className="fb-profile__composer-top">
                <img
                  src={
                    avatarPreview
                      ? avatarPreview
                      : user.avatar && user.avatar.trim() !== ""
                      ? user.avatar
                      : "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQQEjGbsTwEJ2n8tZOeJWLkCivjuYDJBxQbIg&s"
                  }
                  alt="nh i din ca bn"
                />
                <button type="button">Bn ang ngh g th?</button>
              </div>
              <div className="fb-profile__composer-actions">
                <button type="button"> Video trc tip</button>
                <button type="button"> nh/video</button>
                <button type="button"> Cm xc/hot ng</button>
              </div>
            </div>
          )}

          

          <div className="fb-profile__posts">
            {postersLoading ? (
              <div className="loading-text"> ang ti bi vit...</div>
            ) : posts.length === 0 ? (
              <div className="empty-state">Cha c bi vit no.</div>
            ) : (
              posts.map((post, index) => {
                const postId = post.id;
                const getFullName = () => {
                  return post.authorName || 'Ngi dng';
                };
                
                const getTimeAgo = () => {
                  return post.time;
                };

                const getAudienceLabel = () => {
                  if (post.audience === 'public') return ' Cng khai';
                  if (post.audience === 'friends') return ' Bn b';
                  if (post.audience === 'private') return ' Ch mnh ti';
                  return post.audience;
                };

                return (
                  <article 
                    key={postId} 
                    className="fb-post"
                    ref={index === posts.length - 1 ? lastPosterRef : null}
                    style={{ position: 'relative' }}
                  >
                    {/* Loading overlay khi ang xa */}
                    {deletingPost[postId] && (
                      <div style={{
                        position: 'absolute',
                        top: 0,
                        left: 0,
                        right: 0,
                        bottom: 0,
                        backgroundColor: 'rgba(255, 255, 255, 0.8)',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        zIndex: 10,
                        borderRadius: '8px'
                      }}>
                        <div style={{ textAlign: 'center' }}>
                          <div className="spinner" style={{
                            border: '4px solid #f3f3f3',
                            borderTop: '4px solid #3498db',
                            borderRadius: '50%',
                            width: '40px',
                            height: '40px',
                            animation: 'spin 1s linear infinite',
                            margin: '0 auto 10px'
                          }}></div>
                          <p style={{ color: '#333', fontWeight: 'bold' }}>ang xa bi vit...</p>
                        </div>
                      </div>
                    )}
                    <header className="fb-post__header">
                      <img 
                        src={post.authorAvatar || user?.avatar || 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQQEjGbsTwEJ2n8tZOeJWLkCivjuYDJBxQbIg&s'} 
                        alt={getFullName()}
                      />
                      <div>
                        <strong>{getFullName()}</strong>
                        <div className="fb-post__meta">
                          <span>{getTimeAgo()}</span>
                          <span aria-hidden="true"></span>
                          <span>{getAudienceLabel()}</span>
                        </div>
                      </div>
                      
                      {/* Post menu - only show for post owner */}
                      {currentUserRef.current && post.authorId === currentUserRef.current.id && (
                        <div className="fb-post__menu-wrapper">
                          <button 
                            className="fb-post__more" 
                            aria-label="Ty chn bi vit"
                            onClick={() => handleTogglePostMenu(postId)}
                          >
                            
                          </button>
                          
                          {showPostMenu[postId] && (
                            <>
                              <div 
                                className="fb-post__menu-overlay"
                                onClick={() => handleClosePostMenu(postId)}
                              />
                              <div className="fb-post__menu">
                                <button 
                                  type="button"
                                  onClick={() => handleEditPost(postId)}
                                  className="fb-post__menu-item"
                                >
                                  <span className="fb-post__menu-icon"></span>
                                  <span>Chnh sa bi vit</span>
                                </button>
                                <button 
                                  type="button"
                                  onClick={() => handleDeletePostWithMenu(postId, post.authorId, post.isShare)}
                                  className="fb-post__menu-item fb-post__menu-item--danger"
                                >
                                  <span className="fb-post__menu-icon"></span>
                                  <span>Xa bi vit</span>
                                </button>
                              </div>
                            </>
                          )}
                        </div>
                      )}
                    </header>

                    {/* Share content (if this is a share) */}
                    {post.isShare && post.shareContent && (
                      <p className="fb-post__content">{post.shareContent}</p>
                    )}

                    {/* Original content for shares OR regular post content */}
                    {post.isShare && post.originalPosterId ? (
                      <div 
                        className="fb-post__shared-content fb-post__shared-content--clickable"
                        onClick={() => navigate(`/poster/${post.originalPosterId}`)}
                      >
                        <div className="fb-post__shared-header">
                          <img 
                            src={post.originalAuthorAvatar || 'https://images.unsplash.com/photo-1494790108377-be9c29b29330?auto=format&fit=crop&w=80&q=80'}
                            alt={post.originalAuthorName}
                          />
                          <div>
                            <strong>{post.originalAuthorName}</strong>
                            <span className="fb-post__shared-meta">Bi vit gc</span>
                          </div>
                        </div>
                        <p className="fb-post__shared-text">{post.originalContent}</p>
                        
                        {/* Original images */}
                        {post.originalImages && post.originalImages.length > 0 && (
                          <div className="fb-post__shared-media">
                            {post.originalImages.length === 1 ? (
                              <img 
                                src={post.originalImages[0]} 
                                alt="nh gc"
                                className="fb-post__shared-image-single"
                              />
                            ) : (
                              <div className={`fb-post__image-grid ${post.originalImages.length === 2 ? 'fb-post__image-grid--two' : ''}`}>
                                {post.originalImages.slice(0, 4).map((img: string, idx: number) => (
                                  <img 
                                    key={idx}
                                    src={img} 
                                    alt={`nh ${idx + 1}`}
                                  />
                                ))}
                                {post.originalImages.length > 4 && (
                                  <div className="fb-post__image-more">
                                    +{post.originalImages.length - 4}
                                  </div>
                                )}
                              </div>
                            )}
                          </div>
                        )}
                        
                        {/* Original videos */}
                        {post.originalVideos && post.originalVideos.length > 0 && (
                          <div className="fb-post__shared-media">
                            {post.originalVideos.map((video: any, idx: number) => (
                              <video 
                                key={idx}
                                src={video.url} 
                                controls
                                className="fb-post__shared-video"
                                poster={video.thumbnailUrl}
                              >
                                Your browser does not support video.
                              </video>
                            ))}
                          </div>
                        )}
                      </div>
                    ) : (
                      <>
                        {/* Regular post content */}
                        {!post.isShare && <p className="fb-post__content">{post.content}</p>}

                        {/* Render Images for regular posts */}
                        {!post.isShare && post.images && post.images.length > 0 && (
                          <figure className="fb-post__image">
                            {post.images.length === 1 ? (
                              post.images[0].startsWith('data:video/') ? (
                                <video 
                                  src={post.images[0]} 
                                  controls
                                  className="fb-post__video"
                                >
                                  Your browser does not support video.
                                </video>
                              ) : (
                                <img 
                                  src={post.images[0]} 
                                  alt={`nh ca ${getFullName()}`}
                                  onClick={() => openImageViewer(post.images!, 0)}
                                  className="clickable-image"
                                />
                              )
                            ) : (
                              <div className={`fb-post__image-grid ${post.images.length === 2 ? 'fb-post__image-grid--two' : ''}`}>
                                {post.images.slice(0, 4).map((media: string, idx: number) => (
                                  media.startsWith('data:video/') ? (
                                    <video 
                                      key={idx} 
                                      src={media} 
                                      controls
                                      className="fb-post__video-grid"
                                    >
                                      Your browser does not support video.
                                    </video>
                                  ) : (
                                    <img 
                                      key={idx} 
                                      src={media} 
                                      alt={`nh ${idx + 1} ca ${getFullName()}`}
                                      onClick={() => openImageViewer(post.images!, idx)}
                                      className="clickable-image"
                                    />
                                  )
                                ))}
                                {post.images.length > 4 && (
                                  <div 
                                    className="fb-post__image-more clickable-image"
                                    onClick={() => openImageViewer(post.images!, 3)}
                                  >
                                    +{post.images.length - 4}
                                  </div>
                                )}
                              </div>
                            )}
                          </figure>
                        )}

                        {/* Render Videos from videos array for regular posts */}
                        {!post.isShare && post.videos && post.videos.length > 0 && (
                          <figure className="fb-post__image">
                            {post.videos.map((video: any, idx: number) => (
                              <video 
                                key={idx}
                                src={video.url} 
                                controls
                                className="fb-post__video"
                                poster={video.thumbnailUrl}
                              >
                                Your browser does not support video.
                              </video>
                            ))}
                          </figure>
                        )}
                      </>
                    )}

                    <footer className="fb-post__footer">
                      <div className="fb-post__stats">
                        <span className={likeCounts[postId] > 0 ? 'has-reactions' : ''}>
                           {(likeCounts[postId] || 0).toLocaleString('vi-VN')}
                        </span>
                        <span 
                          onClick={() => handleToggleComments(postId)}
                          className="fb-post__stats-clickable clickable-text"
                        >
                          {(commentCounts[postId] || 0)} bnh lun
                        </span>
                        <span>0 lt chia s</span>
                      </div>

                      <div className="fb-post__actions">
                        <button 
                          type="button"
                          className={`fb-post__action-btn ${userLikedPosts[postId] ? 'liked' : ''}`}
                          onClick={() => handleLikeToggle(postId)}
                          disabled={likingInProgress[postId]}
                        >
                          {userLikedPosts[postId] ? '  thch' : ' Thch'}
                        </button>
                        <button 
                          type="button" 
                          className="fb-post__action-btn"
                          onClick={() => handleToggleComments(postId)}
                        >
                           Bnh lun
                        </button>
                        <button type="button" className="fb-post__action-btn"> Chia s</button>
                        {/* <button 
                          type="button" 
                          onClick={() => navigate(`/poster/${postId}`)}
                          className="fb-post__action-btn btn-view-detail"
                        >
                           Xem chi tit
                        </button>
                        {currentUserRef.current && poster.idUser === currentUserRef.current.id && (
                          <button 
                            type="button" 
                            onClick={() => handleDeletePost(postId, poster.idUser)}
                            className="fb-post__action-btn btn-delete"
                          >
                             Xa
                          </button>
                        )} */}
                      </div>

                      {/* Comments Section */}
                      {showComments[postId] && (
                        <div className="fb-post__comments">
                          {/* Comment Input */}
                          <div className="fb-comment-input">
                            <img 
                              src={currentUserRef.current?.avatar || 'https://images.unsplash.com/photo-1494790108377-be9c29b29330?auto=format&fit=crop&w=80&q=80'}
                              alt="Your avatar"
                              className="fb-comment-input__avatar"
                            />
                            <div className="fb-comment-input__field">
                              <input
                                type="text"
                                placeholder="Vit bnh lun..."
                                value={commentInputs[postId] || ''}
                                onChange={(e) => setCommentInputs(prev => ({ ...prev, [postId]: e.target.value }))}
                                onKeyPress={(e) => {
                                  if (e.key === 'Enter' && !submittingComment[postId]) {
                                    handleSubmitComment(postId);
                                  }
                                }}
                                disabled={submittingComment[postId]}
                              />
                              {commentInputs[postId]?.trim() && (
                                <button
                                  type="button"
                                  onClick={() => handleSubmitComment(postId)}
                                  disabled={submittingComment[postId]}
                                  className="fb-comment-input__submit"
                                >
                                  {submittingComment[postId] ? '...' : ''}
                                </button>
                              )}
                            </div>
                          </div>

                          {loadingComments[postId] ? (
                            <div className="fb-comments-loading">ang ti bnh lun...</div>
                          ) : comments[postId] && comments[postId].length > 0 ? (
                            <div className="fb-comments-list">
                              {comments[postId].map(comment => (
                                <div key={comment.idComment} className="fb-comment">
                                  <NavLink to={`/user/${comment.idUser}`} className="fb-post__author">
                                  <img 
                                    src={comment.userAvatar || 'https://images.unsplash.com/photo-1494790108377-be9c29b29330?auto=format&fit=crop&w=80&q=80'} 
                                    alt={`${comment.userFirstName} ${comment.userLastName}`}
                                    className="fb-comment__avatar"
                                  />
                                  </NavLink>
                                  <div className="fb-comment__content">
                                    {editingComment[comment.idComment] ? (
                                      // Edit Mode
                                      <div className="fb-comment__edit">
                                        <input
                                          type="text"
                                          value={editInputs[comment.idComment] || ''}
                                          onChange={(e) => setEditInputs(prev => ({ ...prev, [comment.idComment]: e.target.value }))}
                                          onKeyDown={(e) => {
                                            if (e.key === 'Enter' && !e.shiftKey) {
                                              e.preventDefault();
                                              handleSubmitEdit(postId, comment.idComment);
                                            } else if (e.key === 'Escape') {
                                              handleToggleEdit(comment.idComment, postId, comment.content);
                                            }
                                          }}
                                          className="fb-comment__edit-field"
                                          disabled={submittingEdit[comment.idComment]}
                                          autoFocus
                                        />
                                        <div className="fb-comment__edit-actions">
                                          <button
                                            type="button"
                                            onClick={() => handleToggleEdit(comment.idComment, postId, comment.content)}
                                            disabled={submittingEdit[comment.idComment]}
                                          >
                                            Hy
                                          </button>
                                          <button
                                            type="button"
                                            onClick={() => handleSubmitEdit(postId, comment.idComment)}
                                            disabled={submittingEdit[comment.idComment] || !editInputs[comment.idComment]?.trim()}
                                            className="btn-primary"
                                          >
                                            {submittingEdit[comment.idComment] ? 'ang lu...' : 'Lu'}
                                          </button>
                                        </div>
                                      </div>
                                    ) : (
                                      // View Mode
                                      <>
                                        <div className="fb-comment__bubble">
                                          <strong>
                                            {comment.userFirstName && comment.userLastName 
                                              ? `${comment.userFirstName} ${comment.userLastName}`.trim()
                                              : comment.userName || 'Ngi dng'}
                                          </strong>
                                          <p>{comment.content}</p>
                                        </div>
                                        <div className="fb-comment__meta">
                                          <span>{formatCommentTime(comment.createdAt)}</span>
                                          <button type="button">Thch</button>
                                          <button 
                                            type="button"
                                            onClick={() => handleToggleReply(comment.idComment, postId)}
                                          >
                                            Phn hi
                                          </button>
                                          {currentUserRef.current?.id === comment.idUser && (
                                            <>
                                              <button 
                                                type="button"
                                                onClick={() => handleToggleEdit(comment.idComment, postId, comment.content)}
                                              >
                                                Sa
                                              </button>
                                              <button 
                                                type="button"
                                                onClick={() => handleDeleteComment(postId, comment.idComment)}
                                              >
                                                Xa
                                              </button>
                                            </>
                                          )}
                                        </div>
                                      </>
                                    )}
                                    
                                    {/* Reply Input */}
                                    {replyingTo[comment.idComment] && (
                                      <div className="fb-reply-input">
                                        
                                        <img 
                                          src={currentUserRef.current?.avatar || 'https://images.unsplash.com/photo-1494790108377-be9c29b29330?auto=format&fit=crop&w=80&q=80'} 
                                          alt="Your avatar" 
                                          className="fb-reply-input__avatar"
                                        />
                                        
                                        <div className="fb-reply-input__field-wrapper">
                                          <input
                                            type="text"
                                            placeholder="Vit phn hi..."
                                            value={replyInputs[comment.idComment] || ''}
                                            onChange={(e) => setReplyInputs(prev => ({ ...prev, [comment.idComment]: e.target.value }))}
                                            onKeyDown={(e) => {
                                              if (e.key === 'Enter' && !e.shiftKey) {
                                                e.preventDefault();
                                                handleSubmitReply(postId, comment.idComment);
                                              }
                                            }}
                                            className="fb-reply-input__field"
                                            disabled={submittingReply[comment.idComment]}
                                          />
                                          {replyInputs[comment.idComment]?.trim() && (
                                            <button
                                              type="button"
                                              onClick={() => handleSubmitReply(postId, comment.idComment)}
                                              disabled={submittingReply[comment.idComment]}
                                              className="fb-reply-input__submit"
                                            >
                                              {submittingReply[comment.idComment] ? '...' : ''}
                                            </button>
                                          )}
                                        </div>
                                      </div>
                                    )}
                                    
                                    {/* Replies - using recursive render */}
                                    {comment.replies && comment.replies.length > 0 && (
                                      <div className="fb-comment__replies">
                                        {comment.replies.map(reply => renderReply(reply, postId, 1))}
                                      </div>
                                    )}
                                  </div>
                                </div>
                              ))}
                            </div>
                          ) : (
                            <div className="fb-comments-empty">Cha c bnh lun no</div>
                          )}
                        </div>
                      )}
                    </footer>
                  </article>
                );
              })
            )}
            
            {/* Loading More Indicator */}
            {loadingMorePosters && (
              <div className="fb-loading-more">
                <div className="fb-spinner"></div>
                <p>ang ti thm bi vit...</p>
              </div>
            )}
            
            {/* No More Posts */}
            {!hasMorePosters && posts.length > 0 && (
              <div className="fb-no-more-posts">
                <p> hin th tt c bi vit</p>
              </div>
            )}
          </div>
        </section>
      </div>
    </div>

    {/* Image Viewer Modal */}
    {viewerOpen && (
      <ImageViewer
        images={viewerImages}
        currentIndex={viewerIndex}
        onClose={closeImageViewer}
        onNext={nextImage}
        onPrev={prevImage}
      />
    )}
    </>
  );
};

export default ProfileDetail;
