import React, { useState } from 'react';
import { authenticate, validateLoginForm, isLoggedIn, logout } from '../../api/user/loginApi';
import './LoginPage.css';

const LoginPage: React.FC = () => {
  const [form, setForm] = useState({
    username: '',
    password: ''
  });
  const [isLoading, setIsLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [errors, setErrors] = useState<string[]>([]);
  const [isSuccess, setIsSuccess] = useState(false);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setForm(prev => ({
      ...prev,
      [name]: value
    }));
    // Clear errors when user starts typing
    if (errors.length > 0) {
      setErrors([]);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    setIsLoading(true);
    setMessage('ang ng nhp...');
    setErrors([]);
    setIsSuccess(false);

    // Validate form
    const validationErrors = validateLoginForm(form.username, form.password);
    if (validationErrors.length > 0) {
      setErrors(validationErrors);
      setMessage('Vui lng kim tra li thng tin!');
      setIsLoading(false);
      return;
    }

    try {
      console.log(' Attempting login:', { username: form.username });
      const result = await authenticate(form.username, form.password);
      
      console.log(' Login successful:', result);
      
      setIsSuccess(true);
      setMessage(result.message || 'ng nhp thnh cng! Token  c lu.');
      
      // Redirect sau 2 giy
      setTimeout(() => {
        window.location.href = '/home'; // hoc s dng React Router
      }, 200);
      
    } catch (error: any) {
      console.error(' Login failed:', error);
      setIsSuccess(false);
      setMessage('ng nhp tht bi!');
      setErrors([error.message || 'C li xy ra trong qu trnh ng nhp']);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="login-container">
      <div className="login-card">
        <div className="login-logo">
          <img 
            src="http://res.cloudinary.com/dytdhvf3s/image/upload/v1760508458/User_5e3ca275-4ec3-45c3-8f53-f173871d002e.jpg" 
            alt="ChatWeb Logo" 
          />
        </div>
        <div className="login-header">
          <h2>ng Nhp</h2>
          <p>Cho mng tr li ChatWeb!</p>
        </div>

        <form onSubmit={handleSubmit} className="login-form">
          <div className="form-group">
            <label htmlFor="username">Username:</label>
            <input
              type="text"
              id="username"
              name="username"
              value={form.username}
              onChange={handleInputChange}
              placeholder="Nhp username..."
              disabled={isLoading}
              className="form-input"
            />
          </div>

          <div className="form-group">
            <label htmlFor="password">Password:</label>
            <input
              type="password"
              id="password"
              name="password"
              value={form.password}
              onChange={handleInputChange}
              placeholder="Nhp password..."
              disabled={isLoading}
              className="form-input"
            />
          </div>

          <button 
            type="submit" 
            disabled={isLoading}
            className="login-button"
          >
            {isLoading ? 'ang ng nhp...' : 'ng Nhp'}
          </button>
        </form>

        {/* Message Display */}
        {message && (
          <div className={`message ${isSuccess ? 'success' : 'info'}`}>
            {message}
          </div>
        )}

        {/* Error Display */}
        {errors.length > 0 && (
          <div className="error-container">
            {errors.map((error, index) => (
              <div key={index} className="error-message">
                {error}
              </div>
            ))}
          </div>
        )}

        {/* Links */}
        <div className="login-links">
          <a href="/register">Cha c ti khon? ng k ngay</a>
          <a href="/forgot-password">Qun mt khu?</a>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;