import React, { useState, useEffect } from 'react';
import { activeAccount, validateActivationParams } from '../../api/user/ActiveAccount';
import './ActiveAccountPage.css';

interface PathActivationProps {
  email?: string;
  activationCode?: string;
}

const PathActivationPage: React.FC<PathActivationProps> = () => {
  const [form, setForm] = useState({
    email: '',
    activationCode: ''
  });
  const [message, setMessage] = useState<string>('ang x l kch hot ti khon...');
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [errors, setErrors] = useState<string[]>([]);
  const [isSuccess, setIsSuccess] = useState<boolean>(false);

  const handleActivation = async (email: string, code: string) => {
    setIsLoading(true);
    setMessage('ang kch hot ti khon...');
    setErrors([]);
    setIsSuccess(false);

    console.log('Attempting activation with:', { email, code });

    // Validate
    const validationErrors = validateActivationParams(email, code);
    if (validationErrors.length > 0) {
      setErrors(validationErrors);
      setMessage('Thng tin kch hot khng hp l!');
      setIsLoading(false);
      return;
    }

    try {
      const result = await activeAccount(email, code);
      
      if (result.success) {
        setMessage(' Kch hot ti khon thnh cng! Bn c th ng nhp ngay by gi.');
        setIsSuccess(true);
        setErrors([]);
      } else {
        setMessage(` ${result.message}`);
        setErrors([result.message]);
      }
    } catch (error) {
      console.error('Activation error:', error);
      const errorMsg = error instanceof Error ? error.message : 'C li xy ra, vui lng th li!';
      setMessage(` ${errorMsg}`);
      setErrors([errorMsg]);
    }

    setIsLoading(false);
  };

  useEffect(() => {
    // Parse URL path  ly email v activation code
    const pathSegments = window.location.pathname.split('/');
    console.log('Full URL:', window.location.href);
    console.log('Path segments:', pathSegments);
    
    if (pathSegments.length >= 4 && pathSegments[1] === 'active') {
      // Decode email properly  m bo @ khng b encode
      const rawEmail = pathSegments[2];
      const email = decodeURIComponent(rawEmail).replace('%40', '@');
      const code = pathSegments[3];
      
      console.log('Raw email from path:', rawEmail);
      console.log('Decoded email:', email);
      console.log('Activation code:', code);
      
      setForm({ email, activationCode: code });
      setMessage(' nhn thng tin kch hot t link email');
      
      // T ng kch hot
      handleActivation(email, code);
    } else {
      setMessage(' Link kch hot khng hp l');
      setIsLoading(false);
    }
  }, []);

  const handleRetryActivation = () => {
    if (form.email && form.activationCode) {
      handleActivation(form.email, form.activationCode);
    }
  };

  return (
    <div className="activation-container">
      <div className="activation-box">
        <div className="activation-header">
          <h2> Kch hot ti khon</h2>
        </div>
        
        <div className={`chat-message ${isSuccess ? 'success' : errors.length > 0 ? 'error' : ''}`}>
          {message}
        </div>
        
        {errors.length > 0 && (
          <div className="error-messages">
            {errors.map((error, index) => (
              <div key={index} className="error-message"> {error}</div>
            ))}
          </div>
        )}

        {/* Hin th thng tin ang x l */}
        {form.email && form.activationCode && (
          <div className="activation-info">
            <p><strong>Email:</strong> {form.email}</p>
            <p><strong>M kch hot:</strong> {form.activationCode.substring(0, 8)}...</p>
          </div>
        )}

        {/* Nt th li nu li */}
        {!isLoading && !isSuccess && errors.length > 0 && (
          <button 
            className="activate-btn"
            onClick={handleRetryActivation}
            disabled={isLoading}
          >
             Th li
          </button>
        )}

        {/* Nt ng nhp nu thnh cng */}
        {isSuccess && (
          <div className="success-actions">
            <button 
              className="login-btn"
              onClick={() => window.location.href = '/login'}
            >
               ng nhp ngay
            </button>
          </div>
        )}

        {/* Loading indicator */}
        {isLoading && (
          <div className="loading-indicator">
            <div className="spinner"></div>
            <p>ang x l...</p>
          </div>
        )}
        
        <div className="activation-help">
          <p> Gp vn ?</p>
          <button 
            className="resend-btn" 
            onClick={() => window.location.href = '/contact'}
          >
             Lin h h tr
          </button>
        </div>
      </div>
    </div>
  );
};

export default PathActivationPage;