// Gemini AI API
import { API_BASE_URL } from '../API_BASE_URL';
import { getToken } from '../util/JwtService';

// 

export interface GeminiRequest {
  prompt: string;
}

export interface GeminiResponse {
  response?: string;
  message?: string;
  error?: string;
}

/**
 * Gi cu hi n Gemini AI
 * @param prompt Cu hi/yu cu ca ngi dng
 * @returns Phn hi t Gemini AI
 */
export const askGemini = async (prompt: string): Promise<string> => {
  try {
    const token = getToken();
    
    const response = await fetch(`${API_BASE_URL}/gemini/ask`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        ...(token && { 'Authorization': `Bearer ${token}` })
      },
      body: JSON.stringify({ prompt })
    });

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`HTTP ${response.status}: ${errorText}`);
    }

    // Kim tra content type
    const contentType = response.headers.get('content-type');
    
    if (contentType && contentType.includes('application/json')) {
      const jsonResult: GeminiResponse = await response.json();
      return jsonResult.response || jsonResult.message || 'Khng c phn hi t AI';
    } else {
      // Nu tr v text thun
      const textResult = await response.text();
      return textResult || 'Khng c phn hi t AI';
    }
  } catch (error) {
    console.error('Gemini API error:', error);
    throw error;
  }
};

/**
 * Kim tra prompt c hp l khng
 */
export const validatePrompt = (prompt: string): string | null => {
  if (!prompt || prompt.trim() === '') {
    return 'Vui lng nhp cu hi';
  }
  
  if (prompt.trim().length < 2) {
    return 'Cu hi qu ngn';
  }
  
  if (prompt.length > 2000) {
    return 'Cu hi qu di (ti a 2000 k t)';
  }
  
  return null;
};
