import { API_BASE_URL } from '../API_BASE_URL';
export interface RegisterRequest {
  username: string;
  password: string;
  firstName: string;
  lastName: string;
  email: string;
  phoneNumber: string;
  dateOfBirth: string;
}

export interface RegisterResponse {
  success: boolean;
  message: string;
  data?: {
    id: string;
    username: string;
    email: string;
  };
}

// API ng k ngi dng
export const registerUser = async (userData: RegisterRequest): Promise<RegisterResponse> => {
  try {
    console.log('Sending registration data:', userData);

    const response = await fetch(`${API_BASE_URL}/users/register`, {
  method: 'POST',
  headers: { 'Content-Type': 'application/json' },
  body: JSON.stringify(userData),
});

const contentType = response.headers.get('content-type');
let data;

if (contentType && contentType.includes('application/json')) {
  data = await response.json(); // parse JSON nu backend tr JSON
} else {
  const textData = await response.text(); // parse text nu backend tr chui
  data = {
    success: response.ok,
    message: textData || 'ng k thnh cng!',
  };
}


    if (!response.ok) {
      throw new Error(data.message);
    }

    return data;
  } catch (error) {
    console.error('Register API Error:', error);
    return {
      success: false,
      message: error instanceof Error ? error.message : 'C li xy ra khi ng k',
    };
  }
};


// Validation helper cho form ng k
export const validateRegisterForm = (formData: RegisterRequest): string[] => {
  const errors: string[] = [];

  if (!formData.username || formData.username.length < 3) {
    errors.push('Tn ng nhp phi c t nht 3 k t');
  }

  if (!formData.password || formData.password.length < 6) {
    errors.push('Mt khu phi c t nht 6 k t');
  }

  if (!formData.firstName.trim()) {
    errors.push('H khng c  trng');
  }

  if (!formData.lastName.trim()) {
    errors.push('Tn khng c  trng');
  }

  if (!formData.email || !isValidEmail(formData.email)) {
    errors.push('Email khng hp l');
  }

  if (!formData.phoneNumber || formData.phoneNumber.length < 10) {
    errors.push('S in thoi phi c t nht 10 s');
  }

  if (!formData.dateOfBirth) {
    errors.push('Ngy sinh khng c  trng');
  }

  return errors;
};

// Helper function  validate email
const isValidEmail = (email: string): boolean => {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  return emailRegex.test(email);
};
